:root{
    --primaryColor: #EB2590;
    --textPrimary: #DD2387;
    --fadedPrimary: #fbf0f7;
    --textSecondary: #535659;
    --secondaryBlack: #000000;
    --white: #FFFFFF;
    font-family: 'Lato';
}
*img{
    object-fit: cover;
    object-position: center;
    width: 20px;
}
body{
    background-color: #FFFFFF;
    /* background-color: #EB2590; */
    max-width: 1440px;
    display: flex;
    flex-direction: column;
    font-family: 'Lato';
}
.app-redesign{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    font-family: 'Lato';

}

/* Nav bar */
.navBar-container{
    width: 95%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 50px;
    margin-bottom: 200px; 
    /* this margin, you might remove it when you get the actual height and can use juc- space-between. */
}


.nav-list ul{
    display: flex;
    width:auto;
    padding: 0;
    align-items: center;
    margin-bottom: 0;
    justify-content: space-between;
    grid-gap: 30px;
    gap: 30px;
}
li.nav-list-items{
    font-weight: 400;
    font-size: 20px;
    line-height: 153.2%;
    list-style: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: #535659;
    color: var(--textSecondary);
}
.logo-container img{
    width: 196.1px;
}

.sign-in-btn{
    width: 190px;
    height: 55px;
    border: 1px solid #EB2590;
    border-radius: 6px;
    background-color: #FFFFFF;
    background-color: var(--white);
    color: #DD2387;
    color: var(--textPrimary);

    font-size: 20px;
    line-height: 153.2%;
}
.hamburger-div,
.hamburger-img,
#close-icon{
    font-size: 50px;
    visibility: hidden;
    display: none;
}
#sign-in-hidden{
    visibility: hidden;
    display: none;
}

