                 /* FAQ PAGE 


            */
/* .faq-wrapper{
    width: 95%;
} */

.faq-header{
    width: 100%;
    /* height: 560px; */
    padding-left: 50px;
    padding-right: 50px;

    background: #FFF8F8;
}
.faq-header-content{
    width: 80%;
    padding-bottom: 100px;
}

.faq-title{
    font-family: 'Playfair Display';
    font-weight: 400;
    font-size: 60px;
    line-height: 114%;
    text-align: center;
    color: #283655;
    margin-bottom: 54px;
}
.search-box-div{
    position: relative;
    min-width: 300px;
    width: 100%;
    height: 90px;

    background: #FFFFFF;
    border: 1px solid #ED1F8E;
    border-radius: 7px;
}
.search-box-div input{
    position: relative;
    height: 100%;
    width: 100%;
    padding: 0 25px;
    outline: none;
    border: none;
    border-radius: 7px;
    padding-left: 80px;
}
.search-box-div input:placeholder-shown{
    font-family: 'Maven Pro', sans-serif !important;
    font-size: 20px;
    line-height: 114%;
    color: #AAAAAA;

}

.search-box-div .search{
    position: absolute;
    height: 100%;
    width: 65px;
    top: 0;
    left: 0;
    padding: 0 0 0 25px;

    background-color: transparent;
    border-radius: 7px 0 0 7px;
}
.search .search-icon {
    font-size: 22px;
    color: #000000;
}

.faq-body{
    width: 80%;
}
.question-number{
    width: 85%;
}
.question-answer-div{
    width: 100%;
    cursor: pointer;
}
.faq-number{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 100%;
    letter-spacing: 0.25px;
    color: rgba(60, 60, 67, 0.5);
    text-align: start;
    margin-bottom: 0;
    height: 32px;
}
.faq-question{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
    display: flex;
    align-items: center;
    color: #000000;
    margin-bottom: 0;
    transition: all .5s cubic-bezier(0, 1, 0, 1);
}
.faq-answer{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 183%;
    color: #3C3C43;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 183%;
    color: #3C3C43;
    transition: all .5s cubic-bezier(0, 1, 0, 1);
    display: none;
    visibility: hidden;
    margin-bottom: 0;

}
.faq-answer.show{
    display: block;
    visibility: visible;
    transition: all .5s cubic-bezier(0, 1, 0, 1);
}
.expand-div{
    height: 48px;
    width: 48px;
    border-radius: 50%;
    cursor: pointer;
    transition: all .5s cubic-bezier(0, 1, 0, 1);
}
.open-div{
    background-color: #F3F5F6;
}
.open-icon{
    font-size: 20px;
    color: #000000;
}
.close-div{
    background: #000000;
}
.close-icon{
    font-size: 20px;
    color: #FFFFFF;
}
.error-page-wrapper{
    width: 100%;
}
