                    /* Media queriess */
@media screen and (max-width: 1363px) {
    .about-figures-number-div-desc {
        width: 230px;
    }
    .about-figures-number-div-desc .title{
        font-size: 70px;
    }
    .about-figures-number-div-desc .desc{
        font-size: 16px;
    }
    .about-hero{
        font-size: 54px;
    }
}
                  
@media screen and (max-width: 1300px) {
    .navBar-container{
        /* width: 90%; */
        margin-bottom: 100px;
    }
    .contact-info{
        width: 300px;
    }
    .error-content{
        margin-bottom: 100px;
    }
 
